.navbar {
display: flex;
justify-content: space-between;
align-items: center;
padding: 2rem 3rem;



}
.navCon {
flex: 1;
display: flex;
justify-content: flex-start;
align-items: center;

}
.nav p {
    
    font-size: 80%;
    transition: all 400ms ease-in-out;
    border-radius: 7px;
    padding: 0.3rem;
    text-align: left;
    
}
.nav a {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 5px;
}
.nav p:hover {
    background: white;
    color: black;
}
.navLogo {
margin-right: 2rem;

}
.navLogo img {
width: 62.56px;
height: 16.02px;

}
.navLinks {
    display: flex;
    
}
.navSign {
display: flex;
justify-content: flex-end;
align-items: center;


}
.navLinks p, .navSign p, .navMenu p, .navMenuCon p {

    color: #fff;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    text-transform: capitalize;
    margin: 0 1rem;
    cursor: pointer;
}
.navSign button, .navMenuCon button, .navMenu button {
padding: 0.5rem 1rem;
color: #fff;
background: #ff4820;
border-radius: 5px;
font-family: var(--font-family);
font-weight: 500;
font-size: 18px;
line-height: 25px;
border: none;
outline: none;
cursor: pointer;
}
.navMenu {
    position: relative;
    display: none;
    
}
.navMenuCon {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    flex-direction: column;
    text-align: center;
    background: var(--color-footer);
    left: 0;
    position: absolute;
    top: 73px;
    width: 100vw;
    margin-top: 1rem;
    min-width: 210px;
    border-radius: 5px;
    box-shadow: 0 0 5 rgba(0,0,0,0.2);
}
.navMenuCon p {
margin: 1rem 0;


}
.navMenuSign {
display: none;

}
@media screen and (max-width:1050px){
.navLinks {
    display: none;
}
.navMenu {
    display: flex;
    justify-content: flex-end;
}


}
@media screen and (max-width:700px){
    .navbar {
        padding: 2rem 0;
    }
    .navMenu {
        display: flex;
        justify-content: flex-end;
    }
   
    
    }
    @media screen and (max-width:550px){
        .navbar {
            padding: 2rem 0;
        }
        .navSign{
            display: none;
        }
        
        .navMenuSign {
            display: block;
        }
        .navMenu {
            display: flex;
            justify-content: flex-end;
        }
       
        
        }