.article {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    background: var(--color-text);
}
.articleImg {
    width: 100%;
    height: 100%;
}
.articleImg img {
    width: 100%;
    height: 100%;
}
.articleCon {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 1rem 1.5rem;
    height: 100%;
    
}
.articleCon p {
    font-size: 11.69px;
    font-family: var(--font-family);
    font-weight: bold;
    color: #FFFFFF;
}
.articleCon h3 {
    font-size: 20px;
    font-family: var(--font-family);
    margin-bottom: 3rem;
    margin-top: 0.5rem;
    font-weight: 600;
    color: #FFFFFF;
    
}
.articleCon p:last-child {
    cursor: pointer;
}
@media screen and (max-width: 550px){
    .article h3{
            font-size: 18px;
            line-height: 25px;
    }
}