.FeaturesConFeature {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-direction: row;
    margin: 1rem 0;


}
.FeaturesConFeatureTitle {
    flex: 1;
    max-width: 180px;
    margin-right: 2rem;
}
.FeaturesConFeature h1 {
    font-size: 18px;
    font-family: var(--font-family);
    line-height: 24px;
    max-width: 510px;
    font-weight: 800;
    color: white;


}
.FeaturesConFeatureTitle div {
    width: 38px;
    height: 3px;
    background: var(--gradient-bar);
    margin-bottom: 0.25rem;
}
.FeaturesConFeatureTxt {
    flex: 2;
    display: flex;
    max-width: 390px;


}
.FeaturesConFeatureTxt {
        font-size: 14px;
        font-family: var(--font-family);
        line-height: 24px;
        font-weight: 500;
        color: var(--color-text);
    


    }


@media screen and (max-width: 550px) {
    .FeaturesConFeatureTitle h1 {
        font-size: 14px;
        line-height: 22px;

    }
    .FeaturesConFeatureTitle p {
        font-size: 12px;
        line-height: 20px;

    }
    .FeaturesConFeature {
        margin: 1rem 0;
    }


}