.cta {

    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 2rem;
    margin: 3rem;
    border-radius: 1rem;
    background: var(--gradient-bar);

}
.ctaCon p {
    font-size: 12px;
    font-family: var(--font-family);
    line-height: 30px;
    
    font-weight: 500;
    color: #DE0E0E;
}
.ctaCon h3 {
    font-size: 24px;
    font-family: var(--font-family);
    line-height: 45px;
    
    font-weight: 800;
    color: #000000;
}
.ctaBtn {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 2rem;
}
.ctaBtn button {
    background: #000000;
    border-radius: 40px;
    font-size: 18px;
    font-family: var(--font-family);
    line-height: 30px;
    color: #FFFFFF;
    padding: 0.5rem 1rem;
    border: none;
    outline: none;
    min-width: 150px;
    cursor: pointer;
    
    font-weight: 500;
}
@media screen and (max-width: 650px){
    .cta {
        flex-direction: column;
    }
    .ctaBtn {
        margin: 2rem 0 0;
    }
}
@media screen and (max-width: 550px) {

    .cta {
        margin: 4rem 2rem;
    }
    .ctaCon h3 {
        font-size: 18px;
        line-height: 32px;
    }
    .ctaBtn button {
        font-size: 14px;
        line-height: 28px;
    }
}